import { useEffect, useRef, useCallback, useState } from 'react';
import { autoWebViewJs } from '@droid/android';
import toast from 'react-hot-toast';

const EVENT_SHAKE_PERMISSIONS_COMPLETE = "shakePermissionsComplete";

function requestAllPermissions(callback: (success: boolean) => void) {
    const script = `com.fanfanlo.emergencycall.v4.permission.ShakePermissionUtils.requestAllShakePermissions()`;
    autoWebViewJs.callScript(script);
    
    function handler(event: Event) {
        const customEvent = event as CustomEvent;
        const detail = customEvent.detail || {};
        
        // success
        const success = !!detail.success;
        
        callback(success);
        window.removeEventListener(EVENT_SHAKE_PERMISSIONS_COMPLETE, handler);
    }
    
    window.addEventListener(EVENT_SHAKE_PERMISSIONS_COMPLETE, handler);
    
    // 
    return () => {
        window.removeEventListener(EVENT_SHAKE_PERMISSIONS_COMPLETE, handler);
    };
}

/**
 *  Hook
 */
export function useShakePermissions() {
    const [isRequesting, setIsRequesting] = useState(false);
    const cleanupRef = useRef<(() => void) | null>(null);

    const requestPermissions = useCallback(async (): Promise<boolean> => {
        if (isRequesting) {
            console.log('...');
            return false;
        }

        setIsRequesting(true);

        return new Promise<boolean>((resolve) => {
            try {
                // 
                if (cleanupRef.current) {
                    cleanupRef.current();
                    cleanupRef.current = null;
                }

                cleanupRef.current = requestAllPermissions((success) => {
                    setIsRequesting(false);

                    // 
                    if (success) {
                        toast.success('');
                    } else {
                        toast.error('');
                    }

                    resolve(success);
                });

            } catch (error) {
                console.error(':', error);
                setIsRequesting(false);
                toast.error('');
                resolve(false);
            }
        });
    }, [isRequesting]);

    // 
    useEffect(() => {
        return () => {
            if (cleanupRef.current) {
                cleanupRef.current();
            }
        };
    }, []);

    return {
        isRequesting,
        requestPermissions
    };
}